import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';

export default class DecorationStoreStatusModal extends Modal {
  static isDismissibleViaBackdropClick = false;
  static isDismissibleViaCloseButton = true;

  oninit(vnode) {
    super.oninit(vnode);
    this.decorationStoreData = this.attrs.decorationStoreData;
    this.isActivate = this.attrs.isActivate;
    this.loading = false;
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return app.translator.trans(this.isActivate===0?'wusong8899-decoration-store.admin.item-deactivate-confirmation':'wusong8899-decoration-store.admin.item-activate-confirmation');
  }

  content() {
    //
    return (
      <div className="Modal-body">
        <div className="Form-group" style="text-align: center;">
          {Button.component(
            {
              className: 'Button Button--primary',
              type: 'submit',
              loading: this.loading,
            },
            app.translator.trans('wusong8899-decoration-store.lib.confirm')
          )}&nbsp;
          {Button.component(
            {
              className: 'Button decorationButton--gray',
              loading: this.loading,
              onclick: () => {
                this.hide();
              }
            },
            app.translator.trans('wusong8899-decoration-store.lib.cancel')
          )}
        </div>
      </div>
    );
  }

  onsubmit(e) {
    e.preventDefault();

    this.loading = true;

    this.decorationStoreData.save({
      isActivate:this.isActivate
    })
    .then(() => {
        this.loading = false;
        this.hide();
        m.redraw();
      },
      (response) => {
        this.loading = false;
      }
    );

  }
}
