import {extend, override} from 'flarum/extend';
import DecorationStoreSettings from './components/DecorationStoreSettings';
import DecorationStore from "../forum/model/DecorationStore";
import DecorationStoreGallery from "../forum/model/DecorationStoreGallery";

app.initializers.add('wusong8899-decoration-store', () => {
  app.store.models.decorationStoreList = DecorationStore;
  app.store.models.decorationStoreGallery = DecorationStoreGallery;
  app.extensionData.for('wusong8899-decoration-store').registerPage(DecorationStoreSettings);
});
