import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';
import DecorationStoreAddModal from './DecorationStoreAddModal';

export default class DecorationStoreItemTypeSelectionModal extends Modal {
  static isDismissibleViaBackdropClick = false;
  static isDismissibleViaCloseButton = true;

  oninit(vnode) {
    super.oninit(vnode);
    this.loading = false;
    this.itemTypeList = [
      {id:"avatarFrame"},
      {id:"profileBackground"},
      {id:"usernameColor"},
    ];
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return app.translator.trans('wusong8899-decoration-store.admin.item-type-selection');
  }

  content() {

    //
    return (
      <div className="Modal-body">
        <div className="Form-group" style="text-align: center;">

          {this.itemTypeList.map((itemTypeData) => {
            const itemTypeID = itemTypeData.id;
            const itemTypeText = app.translator.trans("wusong8899-decoration-store.lib.item-type-"+itemTypeID);

            return (
              <div style="padding: 0px 0px 5px 0px;">
                {Button.component({
                    style: 'width:200px;',
                    className: 'Button Button--primary',
                    loading: this.loading,
                    onclick: () => {
                      app.modal.show(DecorationStoreAddModal,{selectedItemType:itemTypeID});
                    }
                  },
                  app.translator.trans(itemTypeText)
                )}&nbsp;
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}
