import ExtensionPage from 'flarum/components/ExtensionPage';
import LoadingIndicator from 'flarum/components/LoadingIndicator';
import Button from 'flarum/components/Button';
import DecorationStoreItemTypeSelectionModal from './DecorationStoreItemTypeSelectionModal'
import DecorationStoreListItem from './DecorationStoreListItem';

import Sortable from 'sortablejs';
import Select from 'flarum/common/components/Select';
import Stream from 'flarum/utils/Stream';

export default class DecorationStoreSettings extends ExtensionPage {
  oninit(attrs) {
    super.oninit(attrs);
    this.loading = true;
    this.moreResults = false;
    this.decorationStoreList = [];

    this.filterActivationStatus = Stream("-1");
    this.filterItemType = Stream("-1");
    this.filterItemPurchaseType = Stream("-1");
    this.loadResults();

    const itemTypeList = app.forum.attribute("decorationStoreItemTypes");
    this.itemTypeOption = {};
    this.itemTypeOption['-1'] = app.translator.trans('wusong8899-decoration-store.lib.item-type-all');

    for(let index in itemTypeList){
      const itemTypeValue = itemTypeList[index];
      this.itemTypeOption[itemTypeValue] = app.translator.trans('wusong8899-decoration-store.lib.item-type-'+itemTypeValue);
    }
  }

  initSort(){
    // let el = document.getElementById('decorationStoreItemsContainer');
    // let sortable = Sortable.create(el,{
    //       animation: 150,
    //       swapThreshold: 0.65,
    //       onEnd: (e) => this.updateSort(e),
    //     });
  }

  content() {
    let loading;

    if(this.loading){
      loading = LoadingIndicator.component({ size: "large" });
    }

    return (
      <div className="ExtensionPage-settings FlarumBadgesPage">
        <div className="container">
          {this.buildSettingComponent({
            type: 'string',
            setting: 'wusong8899-decoration-store.decorationStoreDisplayName',
            label: app.translator.trans('wusong8899-decoration-store.admin.settings.display-name'),
            placeholder:app.translator.trans('wusong8899-decoration-store.admin.settings.display-name-default')
          })}

          {this.buildSettingComponent({
            type: 'string',
            setting: 'wusong8899-decoration-store.decorationStoreTimezone',
            label: app.translator.trans('wusong8899-decoration-store.admin.settings.timezone'),
            help: app.translator.trans('wusong8899-decoration-store.admin.settings.timezone-help'),
            placeholder:app.translator.trans('wusong8899-decoration-store.admin.settings.timezone-default')
          })}

          <div className="Form-group">{this.submitButton()}</div>

          <div style="padding-bottom:20px">
            <Button className={'Button'} disabled={this.loading} onclick={() => app.modal.show(DecorationStoreItemTypeSelectionModal)}>
              {app.translator.trans('wusong8899-decoration-store.admin.item-add')}
            </Button>
          </div>

          <div>
            <div className="DecorationStoreFiltersFlexCard">
              <div className="DecorationStoreFiltersCardsListItem DecorationStoreFiltersCard">
                <Select
                  value={this.filterActivationStatus()}
                  className="DecorationStoreSelectionList" 
                  disabled={this.loading}
                  options={{
                    '-1': app.translator.trans('wusong8899-decoration-store.admin.item-activate-status-all'),
                    '0': app.translator.trans('wusong8899-decoration-store.admin.item-activate-status-down'),
                    '1': app.translator.trans('wusong8899-decoration-store.admin.item-activate-status-up'),
                  }}
                  buttonClassName="Button"
                  onchange={(e) => {
                    this.filterActivationStatus(e);
                    this.filterItem(e)
                  }}
                />
              </div>
              <div className="DecorationStoreFiltersCardsListItem DecorationStoreFiltersCard">
                <Select
                  value={this.filterItemType()}
                  className="DecorationStoreSelectionList" 
                  disabled={this.loading}
                  options={this.itemTypeOption}
                  buttonClassName="Button"
                  onchange={(e) => {
                    this.filterItemType(e);
                    this.filterItem(e)
                  }}
                />
              </div>
              <div className="DecorationStoreFiltersCardsListItem DecorationStoreFiltersCard">
                <Select
                  value={this.filterItemPurchaseType()}
                  className="DecorationStoreSelectionList" 
                  disabled={this.loading}
                  options={{
                    '-1': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-all'),
                    'onetime': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-onetime'),
                    'monthly': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-monthly'),
                    'yearly': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-yearly'),
                  }}
                  buttonClassName="Button"
                  onchange={(e) => {
                    this.filterItemPurchaseType(e);
                    this.filterItem(e)
                  }}
                />
              </div>
            </div>
          </div>

          <ul id="decorationStoreItemsContainer" style="padding:0px;list-style-type: none;" oncreate={this.initSort.bind(this)}>
            {this.decorationStoreList.map((decorationStoreData) => {
              return (
                <li itemID={decorationStoreData.id()} style="margin-top:5px;background: var(--body-bg);">
                  {DecorationStoreListItem.component({ decorationStoreData })}
                </li>
              );
            })}
          </ul>

          {!this.loading && this.decorationStoreList.length===0 && (
            <div>
              <div style="font-size:1.4em;color: var(--muted-more-color);text-align: center;line-height: 100px;">{app.translator.trans("wusong8899-decoration-store.lib.list-empty")}</div>
            </div>
          )}

          {!loading && this.hasMoreResults() && (
            <div style="text-align:center;padding:20px">
              <Button className={'Button Button--primary'} disabled={this.loading} loading={this.loading} onclick={() => this.loadMore()}>
                {app.translator.trans('wusong8899-decoration-store.lib.list-load-more')}
              </Button>
            </div>
          )}

          {loading && <div className="DecorationStore-loadMore">{loading}</div>}

        </div>
      </div>
    );
  }
  
  updateSort(e){
    //
    const newIndex = e.newIndex;
    const oldIndex = e.oldIndex;

    if(newIndex!==oldIndex){
      const children = e.from.children;
      const decorationStoreOrder = {};

      for(let i=0;i<children.length;i++){
        const child = children[i];
        const itemID = $(child).attr("itemID");

        decorationStoreOrder[itemID] = i+1;
      }

      app.request({
        url: `${app.forum.attribute('apiUrl')}/decorationStoreList/order`,
        method: 'POST',
        body: { decorationStoreOrder },
      });
    }
  }

  loadMore() {
    this.loading = true;
    this.loadResults(this.decorationStoreList.length);
  }

  parseResults(results) {
    this.moreResults = !!results.payload.links && !!results.payload.links.next;
    [].push.apply(this.decorationStoreList, results);
    this.loading = false;
    m.redraw();

    return results;
  }

  hasMoreResults() {
    return this.moreResults;
  }

  filterItem(e){
    $("#decorationStoreItemsContainer").empty();
    this.decorationStoreList = [];
    this.loading = true;
    m.redraw();
    this.loadResults();
  }

  loadResults(offset = 0) {
    const filters = {
      isActivate:this.filterActivationStatus(),
      item_type:this.filterItemType(),
      purchase_type:this.filterItemPurchaseType()
    };

    return app.store
      .find("decorationStoreList", {
        filter:filters,
        page: {
          offset,
        },
      })
      .catch(() => {})
      .then(this.parseResults.bind(this));
  }

}
