import Modal from 'flarum/components/Modal';

import DecorationStoreGalleryModal from './DecorationStoreGalleryModal';
import DecorationStoreAddItemType1 from './DecorationStoreAddItemType1';
import DecorationStoreAddItemType2 from './DecorationStoreAddItemType2';

export default class DecorationStoreAddModal extends Modal {
  static isDismissibleViaBackdropClick = false;
  static isDismissibleViaCloseButton = true;

  oninit(vnode) {
    super.oninit(vnode);
    this.itemData = this.attrs.decorationStoreData;
    this.selectedItemType = this.attrs.selectedItemType;
    this.settingType = this.itemData?"edit":"add";
  }

  className() {
    return 'Modal--Large';
  }

  title() {
    return this.settingType==="add"?app.translator.trans('wusong8899-decoration-store.admin.item-add'):app.translator.trans('wusong8899-decoration-store.admin.item-edit');
  }

  content() {
    const itemData = this.itemData;
    const selectedItemType = this.selectedItemType;
    let addModal = DecorationStoreAddItemType1;

    if(selectedItemType==="usernameColor"){
      addModal = DecorationStoreAddItemType2;
    }
    
    return (
      <div className="Modal-body DecorationStoreModalBody">
        {addModal.component({ itemData,selectedItemType })}
      </div>
    );
  }

}
