import Component from "flarum/Component";
import Button from 'flarum/components/Button';
import DecorationStoreAddModal from './DecorationStoreAddModal';
import DecorationStoreDeleteModal from './DecorationStoreDeleteModal';
import DecorationStoreStatusModal from './DecorationStoreStatusModal';

export default class DecorationStoreListItem extends Component {

  oninit(vnode) {
    super.oninit(vnode);
    this.updating = false;
    this.selectedItemType = null;
  }

  view() {
    const {decorationStoreData} = this.attrs;

    const moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';
    const decorationItemID = decorationStoreData.id();
    const decorationItemTitle = decorationStoreData.item_title();
    const decorationItemDesc = decorationStoreData.item_desc();
    const decorationItemType = this.selectedItemType = decorationStoreData.item_type();
    const decorationItemCost = decorationStoreData.item_cost();
    const decorationItemSold = decorationStoreData.item_sold();
    const decorationItemDiscount = decorationStoreData.item_discount();
    const decorationItemDiscountDays = decorationStoreData.item_discount_days();
    const decorationItemDiscountDate = decorationStoreData.item_discount_date();
    const decorationItemAmount = decorationStoreData.item_amount();
    const decorationItemPurchaseType = decorationStoreData.purchase_type();
    const decorationItemCostText = moneyName.replace('[money]', decorationItemCost);
    const decorationItemProperty = JSON.parse(decorationStoreData.item_property());
    const decorationItemImage = decorationItemProperty.image;
    const decorationItemDate = decorationStoreData.assignedAt();
    const decorationItemIsActivate = parseInt(decorationStoreData.isActivate());
    const decorationItemLabelRecommend = decorationStoreData.item_label_recommend();
    const decorationItemLabelPopular = decorationStoreData.item_label_popular();
    
    const addButtonID = "decorationItemAddButton"+decorationItemID;
    const deleteButtonID = "decorationItemDeleteButton"+decorationItemID;
    const activationButtonID = "decorationItemActivationButton"+decorationItemID;

    let decorationItemTypeText = "wusong8899-decoration-store.lib.item-type-"+decorationItemType;
    let decorationItemPurchaseTypeText = "wusong8899-decoration-store.lib.item-purchase-type-"+decorationItemPurchaseType;
    let decorationItemDiscountText = "wusong8899-decoration-store.admin.item-discount-none";
    let decorationItemActivationText = decorationItemIsActivate===0?"wusong8899-decoration-store.admin.item-activate-status-down":"wusong8899-decoration-store.admin.item-activate-status-up";

    if(decorationItemDiscount!==0){
      decorationItemDiscountText = (-decorationItemDiscount)+"%";
    }

    const decorationItemLabelList = [];
    let decorationItemImageStyle = "";

    if(decorationItemType==="profileBackground"){
      decorationItemImageStyle = "background-image:url("+decorationItemImage+")";
    }

    if(decorationItemDiscount!==0) { decorationItemLabelList.push({text:app.translator.trans('wusong8899-decoration-store.lib.item-discount-ribbon',{discount:decorationItemDiscount}),background:"indianred",color:"white"}); }
    if(decorationItemLabelRecommend===1){ decorationItemLabelList.push({text:app.translator.trans('wusong8899-decoration-store.lib.item-label-recommend'),background:"lightskyblue",color:"white"}); }
    if(decorationItemLabelPopular===1){ decorationItemLabelList.push({text:app.translator.trans('wusong8899-decoration-store.lib.item-label-popular'),background:"darkmagenta",color:"white"}); }

    return (
      <div className="decorationItemSettingContainer">
        <div>
          {decorationItemType==="avatarFrame" && (
            <img className="decorationItemAvatarImage" src={decorationItemImage} />
          )}
          {decorationItemType==="profileBackground" && (
            <div className="decorationItemProfileBackgroundImage" style={decorationItemImageStyle}></div>
          )}
          {decorationItemType==="usernameColor" && (
            <div className="decorationItemUsernameColorStyle">
              <div className={decorationItemImage}>{app.translator.trans("wusong8899-decoration-store.admin.item-property-style-text-effect")}</div>
            </div>
          )}
          
          <div className="decorationItemProperty">
            <div className="DecorationStoreItemButtonsFlexCard">
              <div className="DecorationStoreItemButtonsCardsListItem DecorationStoreItemButtonsCard">
                {Button.component({
                  className: 'Button Button--primary',
                  disabled: this.updating,
                  onclick: (e) => {
                    this.editItem(decorationStoreData)
                  }
                },
                app.translator.trans('wusong8899-decoration-store.admin.item-edit')
                )}
              </div>
              &nbsp;

              <div className="DecorationStoreItemButtonsCardsListItem DecorationStoreItemButtonsCard">
                {Button.component({
                  style: "font-weight:bold;",
                  className: 'Button Button--danger',
                  disabled: this.updating,
                  onclick: (e) => {
                    this.deleteItem(decorationStoreData)
                  }
                },
                app.translator.trans('wusong8899-decoration-store.admin.item-delete')
                )}
              </div>
              &nbsp;

              <div className="DecorationStoreItemButtonsCardsListItem DecorationStoreItemButtonsCard">
                {decorationItemIsActivate===1 && (
                  Button.component({
                    style: "font-weight:bold;",
                    className: 'Button',
                    disabled: this.updating,
                    onclick: (e) => {
                      this.activateItem(decorationStoreData,0)
                    }
                  },
                  app.translator.trans('wusong8899-decoration-store.admin.item-deactivate')
                  )
                )}

                {decorationItemIsActivate===0 && (
                  Button.component({
                    style: "font-weight:bold;",
                    className: 'Button',
                    disabled: this.updating,
                    onclick: (e) => {
                      this.activateItem(decorationStoreData,1)
                    }
                  },
                  app.translator.trans('wusong8899-decoration-store.admin.item-activate')
                  )
                )}
              </div>
            </div>

            <div>
              <b>{app.translator.trans('wusong8899-decoration-store.admin.item-id')}: </b>
              {decorationItemID}&nbsp;|&nbsp;
              <b>{app.translator.trans('wusong8899-decoration-store.lib.item-title')}: </b>
              {decorationItemTitle}
            </div>
            
            <div>
              <b>{app.translator.trans('wusong8899-decoration-store.lib.item-desc')}: </b>
              {decorationItemDesc}
            </div>

            <div>
              <b>{app.translator.trans('wusong8899-decoration-store.admin.item-activate-status')}: </b>
              {app.translator.trans(decorationItemActivationText)}&nbsp;|&nbsp;
              <b>{app.translator.trans('wusong8899-decoration-store.lib.item-cost')}: </b>
              {decorationItemCostText}&nbsp;|&nbsp;
              <b>{app.translator.trans('wusong8899-decoration-store.admin.item-discount')}: </b>
              {app.translator.trans(decorationItemDiscountText)}&nbsp;|&nbsp;

              {decorationItemDiscount>0 && (
                <span>
                  <b>{app.translator.trans('wusong8899-decoration-store.admin.item-discount-days')}: </b>
                  {decorationItemDiscountDays}&nbsp;|&nbsp;
                </span>
              )}

              <b>{app.translator.trans('wusong8899-decoration-store.lib.item-amount')}: </b>
              {decorationItemAmount}&nbsp;|&nbsp;
              <b>{app.translator.trans('wusong8899-decoration-store.lib.item-sold')}: </b>
              {decorationItemSold}&nbsp;|&nbsp;
              <b>{app.translator.trans('wusong8899-decoration-store.lib.item-stock-remaining')}: </b>
              {decorationItemAmount-decorationItemSold}
            </div>

            <div>
              <b>{app.translator.trans('wusong8899-decoration-store.lib.item-type')}: </b>
              {app.translator.trans(decorationItemTypeText)}&nbsp;|&nbsp;
              <b>{app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type')}: </b>
              {app.translator.trans(decorationItemPurchaseTypeText)}&nbsp;|&nbsp;
              <b>{app.translator.trans('wusong8899-decoration-store.admin.item-assigned-at')}: </b>
              {decorationItemDate}
            </div>

            {decorationItemLabelList.length>0 && (
              <div>
                <b>{app.translator.trans('wusong8899-decoration-store.lib.item-label')}: </b>
                {decorationItemLabelList.map((labelData) => {
                  const labelText = labelData.text;
                  const labelBackground = labelData.background;
                  const labelColor = labelData.color;

                  let labelStyle = "font-size: 12px;display:inline-block;padding: 2px 6px;background:"+labelBackground+";color:"+labelColor+";";

                  if(labelText!==""){
                    return (
                      <div style={labelStyle} class="DecorationItemLabel">{labelText}</div>
                    );
                  }
                })}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

  activateItem(decorationStoreData,isActivate){
    app.modal.show(DecorationStoreStatusModal, {decorationStoreData,isActivate});
  }

  editItem(decorationStoreData) {
    app.modal.show(DecorationStoreAddModal, {decorationStoreData,selectedItemType:this.selectedItemType});
  }

  deleteItem(decorationStoreData) {
    app.modal.show(DecorationStoreDeleteModal, {decorationStoreData});
  }
}
