import Component from "flarum/Component";
import Button from 'flarum/components/Button';
import Select from 'flarum/common/components/Select';
import Stream from 'flarum/utils/Stream';
import Switch from 'flarum/common/components/Switch';
import Alert from 'flarum/common/components/Alert';
import LoadingIndicator from 'flarum/components/LoadingIndicator';
import DecorationStoreItemTypeSelectionModal from './DecorationStoreItemTypeSelectionModal';
import mobileCheck from "../../forum/common/util/mobileCheck";

export default class DecorationStoreAddItemType1 extends Component {

  oninit(vnode) {
    super.oninit(vnode);

    this.loading = false;
    this.decorationStoreGalleryList = [];
    this.moreResults = false;

    this.itemData = this.attrs.itemData;
    this.selectedItemType = this.attrs.selectedItemType;
    this.settingType = "add";
    this.loadingImage = false;
    this.moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';
    this.isMobile = mobileCheck();

    if(this.itemData){
      this.settingType = "edit";
      this.itemTitle = Stream(this.itemData.item_title());
      this.itemDesc = Stream(this.itemData.item_desc());
      this.itemAmount = Stream(this.itemData.item_amount());
      this.itemCost = Stream(this.itemData.item_cost());
      this.itemDiscount = Stream(this.itemData.item_discount());
      this.itemDiscountDays = Stream(this.itemData.item_discount_days());
      this.itemType = Stream(this.itemData.item_type());
      this.itemPurchaseType = Stream(this.itemData.purchase_type());
      this.itemIsActivate = this.itemData.isActivate()===0?false:true;
      this.itemLabelRecommend = this.itemData.item_label_recommend()===0?false:true;
      this.itemLabelPopular = this.itemData.item_label_popular()===0?false:true;

      const itemProperty = JSON.parse(this.itemData.item_property());
      this.itemImage = Stream(itemProperty.image);

    }else{
      this.itemTitle = Stream("");
      this.itemDesc = Stream("");
      this.itemCost = Stream(1);
      this.itemAmount = Stream(100);
      this.itemDiscount = Stream(0);
      this.itemDiscountDays = Stream(0);
      this.itemType = Stream(this.selectedItemType);
      this.itemPurchaseType = Stream("onetime");
      this.itemImage = Stream("");
      this.itemIsActivate = true;
      this.itemLabelRecommend = false;
      this.itemLabelPopular = false;
    }

    this.modalTitle = this.settingType==="add"?app.translator.trans('wusong8899-decoration-store.admin.item-add'):app.translator.trans('wusong8899-decoration-store.admin.item-edit');
  }

  onModalReady() {
    const _this = this;
    const closeButton = $(".Modal-close .Button");
    $(closeButton).prop('id', 'decorationStoreCloseModalButton');

    const closeButtonClone = closeButton.clone();
    $(closeButtonClone).prop('id', 'decorationStoreCloseGalleryButton');
    $(closeButtonClone).css('display', 'none');
    $(".Modal-close").append(closeButtonClone);

    $(closeButtonClone).on("click", function(){
      _this.closeGallery();
    });
  }

  view() {
    let loading;
    let itemCount = 0;
    let itemCheckNumber = this.isMobile?2:3;
    let itemTypeText = app.translator.trans('wusong8899-decoration-store.lib.item-type-'+this.selectedItemType);
    let itemTypeTextStyle = this.settingType==="edit"?"color:var(--link-color);font-weight: normal;":"font-weight: normal;cursor: pointer;border-bottom: 2px dotted;";

    if(this.loading){
      loading = LoadingIndicator.component({ size: "large" });
    }

    return (
      <div className="Form" oncreate={this.onModalReady.bind(this)}>
        <div id="decorationStoreSettingsContainer" className="Form-group" style="text-align: center;">
          <div style="text-align: left;">
            <div className="decorationStoreSettingsLabel" style="display: flex;align-items: center;">
              <span>{app.translator.trans('wusong8899-decoration-store.admin.item-activate-status')}</span>
              <span style="margin-left: 15px;">
                {Switch.component({
                  state: this.itemIsActivate,
                  onchange: value => {
                      this.itemIsActivate = value;
                  }
                })}
              </span>
            </div>

            <div className="decorationStoreSettingsLabel">
              {app.translator.trans('wusong8899-decoration-store.lib.item-type')}: <span onclick={() => this.selectItemType()} style={itemTypeTextStyle}>{itemTypeText}</span>
            </div>

            <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.lib.item-title')}</div>
            <input maxlength="255" required className="FormControl" bidi={this.itemTitle} />

            <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.lib.item-desc')}</div>
            <textarea maxlength="255" required className="FormControl" bidi={this.itemDesc} />

            <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.lib.item-label')}</div>

            <div style="display: inline-block;">
              {Switch.component({
                state: this.itemLabelRecommend,
                onchange: value => {
                    this.itemLabelRecommend = value;
                }
              }, app.translator.trans("wusong8899-decoration-store.lib.item-label-recommend"))}
            </div>
            <div style="display: inline-block;margin-left: 10px;">
              {Switch.component({
                state: this.itemLabelPopular,
                onchange: value => {
                    this.itemLabelPopular = value;
                }
              }, app.translator.trans("wusong8899-decoration-store.lib.item-label-popular"))}
            </div>

            {this.isMobile===true && (
              <div>
                <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.lib.item-cost')}</div>
                <input required className="FormControl" type="number" step="1" min="1" bidi={this.itemCost} />
                <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.lib.item-amount')}</div>
                <input required className="FormControl" type="number" step="1" min="0" bidi={this.itemAmount} />

                <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.admin.item-discount')}</div>
                <input required className="FormControl" type="number" step="1" min="0" bidi={this.itemDiscount} />
                <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.admin.item-discount-days')}</div>
                <input required className="FormControl" type="number" step="1" min="0" bidi={this.itemDiscountDays} />

                <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type')}</div>
                <Select
                  value={this.itemPurchaseType()}
                  options={{
                    'onetime': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-onetime'),
                    'monthly': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-monthly'),
                    'yearly': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-yearly'),
                  }}
                  buttonClassName="Button"
                  onchange={this.itemPurchaseType}
                />
              </div>
            )}

            {this.isMobile===false && (
              <div>
                <div style="margin-top:15px;font-weight:bold;">
                  <div style="width: 100px;display: inline-block;">{app.translator.trans('wusong8899-decoration-store.lib.item-cost')}</div>
                  <input style="width:150px;display: inline-block;" required className="FormControl" type="number" step="1" min="1" bidi={this.itemCost} />
                  <div style="width: 100px;display: inline-block;margin-left:26px">{app.translator.trans('wusong8899-decoration-store.lib.item-amount')}</div>
                  <input style="width:150px;margin-left:0px;display: inline-block;" required className="FormControl" type="number" step="1" min="0" bidi={this.itemAmount} />
                </div>

                <div style="display:flex;align-items: center;margin-top:15px;font-weight:bold;">
                  <div style="width: 100px;display: inline-block;">{app.translator.trans('wusong8899-decoration-store.admin.item-discount')} (%)</div>
                  <input style="width:150px;display: inline-block;" required className="FormControl" type="number" step="1" min="0" bidi={this.itemDiscount} />
                  <div style="width: 100px;display: inline-block;margin-left:26px">{app.translator.trans('wusong8899-decoration-store.admin.item-discount-days')}</div>
                  <input style="width:150px;margin-left:0px;display: inline-block;" required className="FormControl" type="number" step="1" min="0" max="100" bidi={this.itemDiscountDays} />
                </div>
                
                <div style="margin-top:15px;font-weight:bold;">
                  <div style="width: 100px;display: inline-block;">{app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type')}</div>
                  <Select
                    value={this.itemPurchaseType()}
                    style="font-size:12px;width:150px;"
                    options={{
                      'onetime': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-onetime'),
                      'monthly': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-monthly'),
                      'yearly': app.translator.trans('wusong8899-decoration-store.lib.item-purchase-type-yearly'),
                    }}
                    buttonClassName="Button"
                    onchange={this.itemPurchaseType}
                  />
                </div>
              </div>
            )}
           
            <div className="decorationStoreSettingsLabel">{app.translator.trans('wusong8899-decoration-store.admin.item-property-image')}</div>
            <div style="position:relative">
              <input id="itemImageInput" required className="FormControl" type="text" bidi={this.itemImage} onchange={(e) => this.itemImageUpdate(e)} />
              {Button.component({
                style: "color:var(--button-primary-color);background:var(--button-primary-bg);margin-top: 10px;",
                className: 'Button Button--primary',
                disabled: this.loadingImage,
                onclick: (e) => {
                  this.uploadItemImage(e);
                }
              },
              app.translator.trans('wusong8899-decoration-store.admin.item-property-image-upload')
              )}&nbsp;
              {Button.component({
                style: "color:var(--button-primary-color);background:var(--button-primary-bg);margin-top: 10px;",
                className: 'Button Button--primary',
                disabled: this.loadingImage,
                onclick: (e) => {
                  this.showGallery(e);
                }
              },
              app.translator.trans('wusong8899-decoration-store.admin.item-property-image-select-from-gallery')
              )}
            </div>
            <div className="decorationStoreSettingsLabel">
              <img id="itemImageContainer" src={this.itemImage()} style="height:100px;" />
            </div>
          </div>

          <div>
            {Button.component({
                className: 'Button Button--primary',
                loading: this.loading,
                onclick: (e) => {
                  // app.modal.close();
                  this.submitData(e);
                }
              },
              this.settingType==="add"?app.translator.trans('wusong8899-decoration-store.admin.data-add'):app.translator.trans('wusong8899-decoration-store.admin.data-save')
            )}&nbsp;
            {Button.component({
                className: 'Button decorationButton--gray',
                loading: this.loading,
                onclick: () => {
                  // app.modal.close();
                  $(".Modal-close .Button").click();
                }
              },
              app.translator.trans('wusong8899-decoration-store.lib.cancel')
            )}
          </div>
        </div>

        <div id="decorationStoreSettingsGalleryContainer" style="display:none">
          <div className="DecorationGalleryListFlexCard">
            {this.decorationStoreGalleryList.map((galleryData) => {
              let galleryClass = "DecorationGalleryListCardsListItem DecorationGalleryListCard";
              let galleryUrl = galleryData.url();
              let galleryCount = galleryData.count();
              let itemType = this.selectedItemType;
              let galleryItemInUseText = galleryCount===0?"":app.translator.trans("wusong8899-decoration-store.admin.gallery-image-in-use",{count:galleryCount});

              if(itemCount%itemCheckNumber!==0){
                galleryClass+=" DecorationGalleryListCardAdjust";
              }

              let itemPropertyImage = "assets/extensions/wusong8899-decoration-store/"+itemType+"/"+galleryUrl;
              let itemImageStyle = "";

              if(itemType==="profileBackground"){
                itemImageStyle = "background-image:url("+itemPropertyImage+")";
              }

              itemCount++;

              return (
                <div className={galleryClass}>
                  <div className="DecorationGalleryContainer">
                    <div className="DecorationGalleryItemImage" onclick={() => this.selectGalleryItem(itemPropertyImage)}>
                      {itemType==="avatarFrame" && (
                        <img className="DecorationGalleryItemAvatarImage" src={itemPropertyImage} />
                      )}
                      {itemType==="profileBackground" && (
                        <div className="DecorationGalleryItemProfileBackgroundImage" style={itemImageStyle}></div>
                      )}
                    </div>

                    <div className="DecorationGalleryItemImageLabel">{galleryItemInUseText}</div>
                  </div>
                </div>
              );
            })}
          </div>

          {!this.loading && this.decorationStoreGalleryList.length===0 && (
            <div>
              <div style="font-size:1.4em;color: var(--muted-more-color);text-align: center;line-height: 100px;">{app.translator.trans("wusong8899-decoration-store.lib.list-empty")}</div>
            </div>
          )}

          {!loading && this.hasMoreResults() && (
            <div style="text-align:center;padding:20px">
              <Button className={'Button Button--primary'} disabled={this.loading} loading={this.loading} onclick={() => this.loadMore()}>
                {app.translator.trans('wusong8899-decoration-store.lib.list-load-more')}
              </Button>
            </div>
          )}

          {loading && <div className="DecorationStore-loadMore">{loading}</div>}
        </div>
      </div>
    );
  }

  selectItemType(){
    if(this.settingType==="add"){
      app.modal.show(DecorationStoreItemTypeSelectionModal);
    }
  }

  precisionRound(number, precision) {
    let factor = Math.pow(10, precision);
    return Math.round(number * factor) / factor;
  }

  itemImageUpdate(e) {
    $("#itemImageContainer").attr("src",this.itemImage());
  }

  showGallery(){
    $("#decorationStoreSettingsContainer").css("display","none");
    $("#decorationStoreSettingsGalleryContainer").css("display","block");
    $("#decorationStoreCloseGalleryButton").css("display","block");
    $("#decorationStoreCloseModalButton").css("display","none");
    $(".App-titleControl--text").text(app.translator.trans('wusong8899-decoration-store.admin.item-property-image-select-from-gallery'));
    this.decorationStoreGalleryList = [];
    this.loadGallery();
  }

  closeGallery(){
    $("#decorationStoreSettingsContainer").css("display","block");
    $("#decorationStoreSettingsGalleryContainer").css("display","none");
    $("#decorationStoreCloseGalleryButton").css("display","none");
    $("#decorationStoreCloseModalButton").css("display","block");
    $(".App-titleControl--text").text(this.modalTitle);
  }

  selectGalleryItem(url){
    $("#decorationStoreSettingsContainer").css("display","block");
    $("#decorationStoreSettingsGalleryContainer").css("display","none");
    this.itemImage(location.protocol+'//'+location.host+"/"+url);
  }

  loadMore() {
    this.loading = true;
    this.loadGallery(this.decorationStoreGalleryList.length);
  }

  parseResults(results) {
    this.moreResults = !!results.payload.links && !!results.payload.links.next;
    [].push.apply(this.decorationStoreGalleryList, results);
    this.loading = false;
    m.redraw();

    return results;
  }

  hasMoreResults() {
    return this.moreResults;
  }

  loadGallery(offset = 0) {
    this.loading = true;

    return app.store
      .find("decorationStoreGallery", {
        itemType: this.selectedItemType,
        page: {
          offset,
        },
      })
      .catch(() => {})
      .then(this.parseResults.bind(this));
  }

  uploadItemImage(e){
    event.preventDefault();

    const $input = $('<input type="file">');

    $input.appendTo('body').hide().trigger('click').on('change', event => {
      const body = new FormData();
      body.append('decorationItemImage', event.target.files[0]);
      body.append('decorationItemImageType', this.itemType());
      this.loadingImage = true;
      $("#itemImageInput").prop('disabled', true);
      m.redraw();

      app.request({
        url: `${app.forum.attribute('apiUrl')}/decorationItemImageUpload`,
        method: 'POST',
        body,
      }).then(response => {
              this.itemImage(response.path);
              this.itemImageUpdate();
              this.loadingImage = false;
              $("#itemImageInput").prop('disabled', false);
              m.redraw();
          }, error => {
              this.loadingImage = false;
              $("#itemImageInput").prop('disabled', false);
              m.redraw();
              throw error;
          });
    });
  }

  submitData(e) {
    e.preventDefault();

    const itemProperty = {image:this.itemImage()};
    const submitData = {
      item_title:this.itemTitle(),
      item_desc:this.itemDesc(),
      item_cost:this.itemCost(),
      item_discount: this.itemDiscount(),
      item_discount_days: this.itemDiscountDays(),
      item_amount:parseInt(this.itemAmount()),
      item_type:this.itemType(),
      item_label_recommend: this.itemLabelRecommend===false?0:1,
      item_label_popular: this.itemLabelPopular===false?0:1,
      purchase_type:this.itemPurchaseType(),
      item_property:JSON.stringify(itemProperty),
      isActivate:this.itemIsActivate===false?0:1,
    };

    let isValid = true;
    for(let index in submitData){
      if(submitData[index]===""){
        isValid = false;
        break;
      }
    }

    if(isValid===false){
      app.alerts.show(Alert, {type: 'error'}, app.translator.trans('wusong8899-decoration-store.lib.submit-error-field-empty'));
      return;
    }

    this.loading = true;

    if(this.settingType==="edit"){
      this.itemData.save(submitData)
      .then(() => {
          $(".Modal-close .Button").click();
        },
        (response) => {
          this.loading = false;
          this.handleErrors(response);
        }
      );
    }else{
      app.store
        .createRecord("decorationStoreList")
        .save(submitData)
        .then((decorationStoreList) => {
            location.reload();
          }
        )
        .catch((e) => {
          this.loading = false;
          this.handleErrors(decorationStoreList);
        });
    }
  }
}
