import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';

export default class DecorationStoreGalleryModal extends Modal {
  static isDismissibleViaBackdropClick = false;
  static isDismissibleViaCloseButton = true;

  oninit(vnode) {
    super.oninit(vnode);
    this.decorationStoreData = this.attrs.decorationStoreData;
    this.loading = false;
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return app.translator.trans('wusong8899-decoration-store.admin.item-property-image-select-from-gallery');
  }

  content() {
    //
    return (
      <div className="Modal-body">
        <div className="Form-group" style="text-align: center;">
          {Button.component({
              className: 'Button decorationButton--gray',
              loading: this.loading,
              onclick: () => {
                this.hide();
              }
            },
            app.translator.trans('wusong8899-decoration-store.lib.close')
          )}
        </div>
      </div>
    );
  }

  onsubmit(e) {
    e.preventDefault();

    this.loading = true;
    this.decorationStoreData.save({
      isActivate:2,
    })
    .then(
      () => location.reload(),
      (response) => {
        this.loading = false;
        this.handleErrors(response);
      }
    );
  }
}
